raul-alert {
  font-size: 0.875rem;
  display: block;
}

raul-alert.r-alert--fadeout {
  opacity: 0;
  transition: opacity ease 1s;
}

raul-alert .r-alert {
  width: 100%;
  padding: 1rem;
  font-size: 0.875rem;
  height: auto;
}

raul-alert .r-alert__rounded {
  border-radius: 2px;
}

raul-alert .r-alert__information {
  background-color: #E5F4FF;
  border-left: 12px solid #0076CC;
}

raul-alert .r-alert__information a {
  color: #0076CC;
}

raul-alert .r-alert__success {
  background-color: #E7F3EB;
  border-left: 12px solid #139C3E;
}

raul-alert .r-alert__success a {
  color: #139C3E;
}

raul-alert .r-alert__warning {
  background-color: #FEF8EA;
  border-left: 12px solid #FEC12D;
}

raul-alert .r-alert__warning a {
  color: #FEC12D;
}

raul-alert .r-alert__danger {
  background-color: #FAE8E9;
  border-left: 12px solid #D01A1F;
}

raul-alert .r-alert__danger a {
  color: #D01A1F;
}

raul-alert .r-alert__warning a {
  color: #BC8701;
}

raul-alert .r-alert .r-alert__heading {
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

raul-alert .r-alert .r-alert__heading .r-alert__heading__close-button {
  display: flex;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  height: 2rem;
  width: 2rem;
  min-height: 32px;
  min-width: 32px;
  margin-top: -4px;
  margin-right: -8px;
  cursor: pointer;
  color: rgba(32, 39, 55, 0.65);
}

raul-alert .r-alert .r-alert__heading .r-alert__heading__close-button:hover {
  color: #202737;
}

raul-alert .r-alert .r-alert__content {
  padding-bottom: 0.25rem;
  color: rgba(32, 39, 55, 0.8);
}

raul-alert .r-alert a {
  display: inline-block;
  text-decoration: none;
}

raul-alert .r-alert a:focus {
  outline-color: #0076CC;
}